'use client';

import { useFormState } from 'react-dom';
import { useFormStatus } from 'react-dom';
import { createPayment } from '../lib/actions';
import { PaymentSchema } from '../lib/schema';
import { useState } from 'react';

function SubmitButton() {
  const { pending } = useFormStatus();

  return (
    <button
      type="submit"
      disabled={pending}
      className="w-full flex justify-center py-2 px-4 border border-transparent rounded-md shadow-sm text-sm font-medium text-white bg-indigo-600 hover:bg-indigo-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-indigo-500 disabled:opacity-50"
    >
      {pending ? 'Processing...' : 'Pay Now'}
    </button>
  );
}

export default function PaymentForm() {
  const [clientErrors, setClientErrors] = useState<{ [key: string]: string }>({});
  const [state, formAction] = useFormState(createPayment, { error: null, success: false });

  const handleSubmit = async (formData: FormData) => {
    const rawData = {
      amount: Number(formData.get('amount')),
      currency: formData.get('currency'),
      email: formData.get('email')
    };

    const result = PaymentSchema.safeParse(rawData);
    if (!result.success) {
      const errors: { [key: string]: string } = {};
      result.error.issues.forEach((issue) => {
        errors[issue.path[0].toString()] = issue.message;
      });
      setClientErrors(errors);
      return;
    }

    setClientErrors({});
    formAction(formData);
  };

  if (state.success) {
    return (
      <div className="p-6 max-w-sm mx-auto bg-white rounded-xl shadow-md">
        <div className="text-center">
          <h2 className="text-xl font-bold text-green-600">Payment Successful!</h2>
          <p className="mt-2 text-gray-600">Thank you for your payment</p>
        </div>
      </div>
    );
  }

  return (
    <div className="p-6 max-w-sm mx-auto bg-white rounded-xl shadow-md">
      <form action={handleSubmit} className="space-y-4">
        <div>
          <label htmlFor="amount" className="block text-sm font-medium text-gray-700">
            Amount
          </label>
          <input
            type="number"
            id="amount"
            name="amount"
            step="0.01"
            required
            className="mt-1 block w-full rounded-md border-gray-300 shadow-sm focus:border-indigo-500 focus:ring-indigo-500"
          />
          {clientErrors.amount && (
            <p className="mt-1 text-sm text-red-600">{clientErrors.amount}</p>
          )}
        </div>

        <div>
          <label htmlFor="currency" className="block text-sm font-medium text-gray-700">
            Currency
          </label>
          <select
            id="currency"
            name="currency"
            required
            className="mt-1 block w-full rounded-md border-gray-300 shadow-sm focus:border-indigo-500 focus:ring-indigo-500"
          >
            <option value="USD">USD</option>
            <option value="EUR">EUR</option>
            <option value="GBP">GBP</option>
          </select>
          {clientErrors.currency && (
            <p className="mt-1 text-sm text-red-600">{clientErrors.currency}</p>
          )}
        </div>

        <div>
          <label htmlFor="email" className="block text-sm font-medium text-gray-700">
            Email
          </label>
          <input
            type="email"
            id="email"
            name="email"
            required
            className="mt-1 block w-full rounded-md border-gray-300 shadow-sm focus:border-indigo-500 focus:ring-indigo-500"
          />
          {clientErrors.email && (
            <p className="mt-1 text-sm text-red-600">{clientErrors.email}</p>
          )}
        </div>

        {state.error && (
          <div className="text-red-600 text-sm">{state.error}</div>
        )}

        <SubmitButton />
      </form>
    </div>
  );
}